






































































































import Vue from 'vue'
import { ofices } from '../example-data'

const sandboxTemplateHTML = '<v-cascader\n' +
          '  :autocomplete="autocomplete"\n' +
          '  :chips="chips"\n' +
          '  :dense="dense"\n' +
          '  :itemText="name"\n' +
          '  :multiple="multiple"\n' +
          '  :items="items"\n' +
          '  :clearable="clearable"\n' +
          '  :dark="dark"\n' +
          '>\n' +
          '</v-cascader>'

const sandboxCode = '\n' +
'export default ({\n' +
'  data: () => ({\n' +
'    items: ofices, // see data source \n' +
'    chips: false,\n' +
'    multiple: false,\n' +
'    dense: false,\n' +
'    clearable: false,\n' +
'    dark: false,\n' +
'    autocomplete: false,\n' +
'  })\n' +
'})\n'

export default Vue.extend({
  data: () => ({
    items: ofices,
    name: 'name',
    chips: false,
    multiple: false,
    dense: false,
    clearable: false,
    dark: false,
    autocomplete: false,
    //
    codeSandbox: false,
    sandboxTemplate: sandboxTemplateHTML,
    sandboxCode: sandboxCode,
    dataSource: ofices
  })
})
